import React, { memo, useEffect, useState } from 'react';
import { Redirect, Route, Switch } from 'react-router-dom';
import styled from 'styled-components';
import web3 from './contracts/initWeb3';
import { initWeb3, subscribeProvider, web3Modal } from './contracts/web3Model';

import login from "./assets/images/login.png";
import AppBody from './components/AppBody';


import Whitelist from './pages/whitelist';
import Penny from './pages/penny';
import Release from './pages/release';

const LoginButton = styled.button`
    width:131px;
    height:180px;
    background: url(${login}) no-repeat;
    background-size: 100% ;
    position:absolute;
    top:25rem;
    left:calc(50% - 65px);
`

export default memo(function App() {

    const [address, setAddress] = useState(Array)

    useEffect(() => {
        getAddress()
    }, [])

    const getAddress = async () => {
        try {
            const myAddress = await web3.eth.getAccounts()
            setAddress(myAddress)
        } catch (error) {
            alert('该浏览器不支持MetaMesk')
        }
    }

    const onConnectMetaMask = async (): Promise<void> => {
        const provider = await web3Modal.connect();
        await subscribeProvider(provider);
        const web3: any = initWeb3(provider);
        const accounts = await web3.eth.getAccounts();
        setAddress(accounts)
    };

  return (
    <AppBody>
      {
        address.length > 0  ? (<Switch>
            <Route exact={true} path="/whitelist" component={Whitelist}/>
            <Route path="/penny" component={Penny}/>
            <Route path="/release" component={Release}/>
            <Redirect to="/release" />
        </Switch>):(<LoginButton onClick={onConnectMetaMask}/>)
      }
    </AppBody>
  );
})
